.button {
    margin: 0 0 10px 0;
    width: 130px;
    height: 50px;
    border-radius: 5px;
    border: none;
    box-sizing: border-box;
    background-color: #292929;
    font-family: Helvetica;
    font-weight: bold;
    font-size: 16px;
    color: #ffee00;
    cursor: pointer;
    transition: background-color .35s, color .35s linear;
    outline: none; }

.button:hover {
    transition: background-color .35s, color .35s;
    background-color: #ffee00;
    color: #292929; }

.button:last-of-type {
    margin: 0 0 30px 0; }
